  display: none;
}

.tablesaw-bar .btn.tablesaw-nav-btn {
  display: inline-block;
  overflow: hidden;
  width: 1.8em;
  height: 30px;
  background-position: 50% 50%;
  margin-left: .5em;
  position: relative;
}

.tablesaw-bar .btn.tablesaw-nav-btn:before {
  border-color: white;
  content: "\0020";
  overflow: hidden;
  width: 0;
  height: 0;
  position: absolute;
}

.tablesaw-bar .btn.tablesaw-nav-btn.up:before, .tablesaw-bar .btn.tablesaw-nav-btn.down:before {
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  left: .5em;
}

.tablesaw-bar .btn.tablesaw-nav-btn.down:before {
  border-top: 10px solid #808080;
}

.tablesaw-bar .btn.tablesaw-nav-btn.up:before {
  border-bottom: 10px solid;
}

.tablesaw-bar .btn.tablesaw-nav-btn.left:before, .tablesaw-bar .btn.tablesaw-nav-btn.right:before {
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
}

.tablesaw-bar .btn.tablesaw-nav-btn.left:before {
  left: .5em;
  border-right: 10px solid;
}

.tablesaw-bar .btn.tablesaw-nav-btn.right:before {
  left: .64em;
  border-left: 10px solid;
}

.tablesaw-bar .btn.tablesaw-nav-btn.disabled {
  opacity: .25;
  cursor: default;
  pointer-events: none;
}

.tablesaw-advance {
  float: right;
}

.tablesaw-advance.minimap {
  float: none;
  display: inline-block;
}

.tablesaw-advance-dots {
  margin-bottom: 0;
  padding-right: 15px;
}

.tablesaw-advance-dots li {
  display: inline-block;
  margin: 0;
  padding: 0 .2em;
}

.tablesaw-advance-dots i {
  width: .25em;
  height: .25em;
  background: #555;
  border-radius: 100%;
  display: inline-block;
}

.tablesaw-advance-dots-hide {
  opacity: .25;
  cursor: default;
  pointer-events: none;
}

[data-tablesaw-mode="swipe"] > tbody > tr > td {
  vertical-align: middle;
}

[data-tablesaw-mode="swipe"] .tablesaw-cell-persist {
  border-right: 2px solid #e6e6e6;
